.trainers {
  max-width: 1272px;
  margin: 0 auto;
  padding-top: 0;
  padding-bottom: 100px;
  margin-top: 0;
  margin-bottom: 0;
  position: relative;
  background: yellow;
  overflow: hidden;
}

.trainers__control-left {
  border-color: transparent;
  position: absolute;
  border-left: 0;
  top: 236px;
  left: 0;
  width: 0;
  height: 0;
  border-top: 16px solid transparent;
  border-right: 16px solid #afafaf;
  border-bottom: 16px solid transparent;
}

.trainers__control-left::before {
  content: "";
  position: absolute;
  top: -16px;
  left: 1px;
  width: 0;
  height: 0;
  border-top: 16px solid transparent;
  border-right: 16px solid yellow;
  border-bottom: 16px solid transparent;
}

.trainers__control-right {
  border-color: transparent;
  position: absolute;
  top: 236px;
  right: 0;
  width: 0;
  height: 0;
  border-top: 16px solid transparent;
  border-left: 16px solid #afafaf;
  border-bottom: 16px solid transparent;
}

.trainers__control-right::before {
  content: "";
  position: absolute;
  top: -16px;
  right: 1px;
  width: 0;
  height: 0;
  border-top: 16px solid transparent;
  border-left: 16px solid yellow;
  border-bottom: 16px solid transparent;
}

.trainers__control-left:hover,
.trainers__control-right:hover,
.trainers__control-left:focus,
.trainers__control-right:focus {
  cursor: pointer;
}

.trainers__title {
  padding-left: 58px;
  padding-right: 58px;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-size: 49px;
  line-height: 49px;
  font-weight: 700;
  color: #102654;
  text-transform: uppercase;
  letter-spacing: 0.03em;
  margin-top: 0;
  margin-bottom: 36px;
}

.trainers__list {
  max-width: 1160px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: flex;
  position: relative;
  background: green;
  overflow: hidden;

  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
}

.trainers__item {
  width: 260px;
  height: 294px;
  margin-right: 40px;
  position: relative;
}

.trainers__item-image {
  width: 260px;
  height: 294px;
  display: block;
}

.trainers__inner {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding-top: 243px;
  text-align: center;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
}

.trainers__name {
  padding-left: 20px;
  padding-right: 20px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 18px;
  line-height: 27px;
  font-weight: 700;
  color: #ffffff;
  margin-top: 0;
  margin-bottom: -3px;
}

.trainers__text {
  padding-left: 20px;
  padding-right: 20px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: #b6bdcc;
  margin-top: 0;
  margin-bottom: 32px;
  display: none;
}

.trainers__summary-list {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: left;
  padding-left: 70px;
  padding-right: 10px;
  display: none;
}

.trainers__summary-item {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #ffffff;
  margin-bottom: 13px;
  position: relative;
}

.trainers__summary-item::before {
  content: "";
  position: absolute;
  top: 9px;
  left: -15px;
  width: 6px;
  height: 6px;
  background: #ffffff;
  border-radius: 50%;
}

.trainers__item:hover .trainers__inner {
  padding-top: 50px;
  background-color: rgba(16, 38, 84, 0.9);
}

.trainers__item:hover .trainers__text {
  display: block;
}

.trainers__item:hover .trainers__summary-list {
  display: block;
}

@media (max-width: $desktop-width) {
  .trainers {
    max-width: 678px;
    padding-bottom: 80px;
    margin-bottom: 95px;
  }

  .trainers__control-left {
    top: 218px;
  }

  .trainers__control-right {
    top: 218px;
  }

  .trainers__title {
    text-align: center;
    font-size: 42px;
    line-height: 42px;
    margin-bottom: 25px;
  }

  .trainers__list {
    max-width: 566px;
  }

  .trainers__item {
    width: 268px;
    margin-right: 30px;
  }

  .trainers__item-image {
    width: 268px;
    height: 294px;
  }

  .trainers__inner {
    padding-top: 243px;
  }

  .trainers__name {
    padding-left: 13px;
    margin-top: 0;
    margin-bottom: -3px;
  }

  .trainers__text {
    padding-left: 13px;
    margin-bottom: 31px;
  }

  .trainers__summary-list {
    padding-right: 20px;
  }

  .trainers__item:hover .trainers__inner {
    padding-top: 51px;
  }
}

@media (max-width: $tablet-width) {
  .trainers {
    max-width: 290px;
    padding-bottom: 30px;
    margin-bottom: 20px;
  }

  .trainers__control-left {
    top: 207px;
    border-top: 9px solid transparent;
    border-right: 10px solid #afafaf;
    border-bottom: 9px solid transparent;
  }

  .trainers__control-left::before {
    top: -9px;
    left: 1px;
    border-top: 9px solid transparent;
    border-right: 10px solid yellow;
    border-bottom: 9px solid transparent;
  }

  .trainers__control-right {
    top: 207px;
    border-top: 9px solid transparent;
    border-left: 10px solid #afafaf;
    border-bottom: 9px solid transparent;
  }

  .trainers__control-right::before {
    top: -9px;
    right: 1px;
    border-top: 9px solid transparent;
    border-left: 10px solid yellow;
    border-bottom: 9px solid transparent;
  }

  .trainers__title {
    font-size: 32px;
    line-height: 32px;
    margin-bottom: 26px;
  }

  .trainers__list {
    max-width: 226px;
  }

  .trainers__item {
    width: 226px;
    height: 274px;
  }

  .trainers__item-image {
    width: 226px;
    height: 274px;
  }

  .trainers__inner {
    padding-top: 220px;
  }

  .trainers__name {
    padding-left: 20px;
  }

  .trainers__text {
    padding-left: 20px;
    margin-bottom: 32px;
  }

  .trainers__summary-list {
    padding-left: 49px;
  }

  .trainers__item:hover .trainers__inner {
    padding-top: 40px;
  }
}
